
<template>
  <div style="background-color:#F6F6F6;position:fixed;top:50px;bottom:0;left:0;right:0;overflow-y:scroll;">
    <div>
      <div
        class="box"
        @click="btnD(info.length,info.length-i,item)"
        v-for="(item,i) in info"
        :key="i"
      >
        <!-- 周期 -->
        <div class="round">
          <p class="di">第 {{ info.length-i }} 期</p>
          <div class="di_box">

            <p
              v-if="item.status==0"
              style="color:#FB9E58;"
            >待提交</p>
            <p
              v-else-if="item.status==1 && item.payStatus==0"
              style="color:#02BC6C;"
            >已提交</p>
            <p
              v-else-if="item.payStatus==1"
              style="color:#02BC6C;"
            >
              <span style="color:#313443;font-size:0.6rem;margin-right:4px;">
                <span v-if="item.payment">({{payments[item.payment]}})</span>
              </span>已缴费
            </p>
            <p
              v-else-if="item.payStatus == 0 && item.status == 1"
              style="color:#FB9E58;"
            >待缴费</p>
            <p
              v-else-if="newT>item.endTime && item.status==1"
              class="dai"
              style="color:#FF4848;"
            >已逾期</p>
          </div>
        </div>
        <!-- 费用周期 -->
        <div class="riqi">
          <p class="riqi_l">费用周期</p>&emsp;&emsp;
          <p class="riqi_r">{{ item.startTime }}至{{ item.endTime }}</p>
        </div>
        <div class="totle">总计 &yen; {{ item.totalPrice }}</div>
      </div>
    </div>
    <div
      class="box"
      @click="btnFirst(item)"
      v-for="(item,index) in firstInfo"
      :key="index"
    >
      <div class="round">
        <p class="dai">起始读数</p>
        <p
          v-if="item.status==1 && item.payStatus !=1"
          class="back"
          @click="first_back(item)"
        >撤回</p>
      </div>
      <div class="riqi_box">
        <p class="riqi_l">记录时间</p>
        <p>{{ item.createTime }}</p>
      </div>
      <div
        class="riqi_box"
        v-if="item.elecPrice != 0"
      >
        <p class="riqi_l">电表读数</p>
        <p>{{ item.elecDegree }}kwh</p>
      </div>
      <div
        class="riqi_box"
        v-if="item.waterPrice != 0"
      >
        <p class="riqi_l">水表读数</p>
        <p>{{ item.waterDegree }}吨</p>
      </div>
      <div
        class="riqi_box"
        v-if="item.gasPrice != 0"
      >
        <p class="riqi_l">气费读数</p>
        <p>{{ item.gasDegree }}度</p>
      </div>
    </div>
    <div
      v-if="nothing_show"
      class="nothing"
    >暂未开始生活缴费</div>
    <!-- 弹出框 -->
    <mt-popup
      v-model="first_show"
      position="center"
      style="color: #000;width:90%;border-radius:10px;font-size: 0.8rem;"
    >
      <p class="put_box">输入起始读数</p>
      <div
        class="ele_box"
        v-if="show_info.elecPrice != 0"
      >
        <div>电表读数</div>&emsp;&emsp;
        <input
          type="number"
          placeholder="请输入"
          v-model="first_ele"
          class="input_box"
          id="ele"
        />
      </div>
      <div
        class="ele_box"
        v-if="show_info.waterPrice != 0"
      >
        <div>水表读数</div>&emsp;&emsp;
        <input
          type="number"
          placeholder="请输入"
          v-model="first_water"
          class="input_box"
          id="water"
        />
      </div>
      <div
        class="ele_box"
        v-if="show_info.gasPrice != 0"
      >
        <div>气表读数</div>&emsp;&emsp;
        <input
          type="number"
          placeholder="请输入"
          v-model="first_gas"
          class="input_box"
          id="gas"
        />
      </div>
      <div class="first_bottom">
        <div
          class="first_canal"
          @click="first_cancal"
        >取消</div>
        <div
          class="first_ok"
          @click="first_ok"
        >确定</div>
      </div>
    </mt-popup>
    <!-- 撤回 -->
    <mt-popup
      v-model="back_box"
      style="width:80%;border-radius:10px;"
    >
      <div class="edit_box">
        <div class="isBack">是否撤回该账单？</div>
        <div class="back_bottom">
          <p
            class="first_canal"
            @click="back_cancel"
          >取消</p>
          <p
            class="first_ok"
            @click="back_ok"
          >确定</p>
        </div>
      </div>
    </mt-popup>
  </div>
</template>
<script>
import { GetSystemDictDataByType } from '@/api/api_query';
import { PostBusinessLifeCostBillOriginEdit, GetBusinessLifeCostBillByorderroom, PostBusinessLifeCostBillRecall } from '@/api/api_business_hotel_and_scale';
export default {
    data() {
        return {
            info: [],
            firstInfo: [],
            newT: '',
            show: false,
            ele: '',
            water: '',
            gas: '',
            first_show: false,
            first_ele: '',
            first_water: '',
            first_gas: '',
            back_box: false,
            nothing_show: false,
            show_info: '',
            payments: {}
        };
    },
    created() {
        this.$store.commit('setHeader', '生活账单');
        this.newT = new Date().getFullYear() + '-' + (new Date().getMonth() + 1 < 10 ? '0' + (new Date().getMonth() + 1) : new Date().getMonth() + 1) + '-' + (new Date().getDate() < 10 ? '0' + new Date().getDate() : new Date().getDate());
        this.id = this.$route.query.id;
        this.getinfo();
        this.payment();
    },
    methods: {
        payment() {
            GetSystemDictDataByType({ dictType: 'business_payment' }).done((res) => {
                let payments = {};
                for (var i = 0; i < res.data.length; i++) {
                    payments[res.data[i].dictSort] = res.data[i].dictLabel;
                }
                this.payments = payments;
            });
        },
        // 查询生活账单
        getinfo() {
            GetBusinessLifeCostBillByorderroom(this.id).done((res) => {
                if (res.data.length == 0) {
                    this.nothing_show = true;
                } else {
                    this.firstInfo = res.data.filter((item) => item.isOrigin == 1);
                    this.info = res.data.filter((item) => item.isOrigin !== 1).reverse();
                }
            });
        },
        btnD(length, i, item) {
            localStorage.removeItem('life_fee');
            this.$router.push({
                path: '/lifeFeetrans',
                query: {
                    lifeCostBillId: item.id,
                    set_length: item.length,
                    set_round: i
                }
            });
        },
        // 编辑起始读数-弹出框
        btnFirst(item) {
            if (item.elecDegree) {
                this.first_ele = item.elecDegree;
                document.getElementById('ele').disabled = 'true';
            } else {
                this.first_ele = '';
            }
            if (item.waterDegree) {
                this.first_water = item.waterDegree;
                document.getElementById('water').disabled = 'true';
            } else {
                this.first_water = '';
            }
            this.show_info = item;
            this.first_id = item.id;
            if (item.status != 1) {
                this.first_show = true;
            }
        },
        // 取消
        first_cancal() {
            this.first_show = false;
        },
        // 编辑-起始读数
        first_ok() {
            let data = {
                id: this.first_id,
                elecDegree: this.first_ele,
                waterDegree: this.first_water,
                gasDegree: this.first_gas
            };
            PostBusinessLifeCostBillOriginEdit(data).done((res) => {
                this.first_show = false;
                this.getinfo();
            });
        },
        // 撤回
        first_back(item) {
            this.back_id = item.id;
            this.back_box = true;
        },
        back_cancel() {
            this.back_box = false;
        },
        // 撤回
        back_ok() {
            PostBusinessLifeCostBillRecall(this.first_id ? this.first_id : this.back_id).done((res) => {
                this.back_box = false;
                this.getinfo();
            });
        }
    }
};
</script>
<style scoped>
.riqi_box {
    display: flex;
    justify-content: space-between;
}
.box {
    margin: 15px 0;
    background: #fff;
    font-size: 0.8rem;
    line-height: 30px;
    padding: 0 10px;
}
.round {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
}
.di {
    font-weight: bold;
}
.di_box {
    display: flex;
    align-items: baseline;
}
.dai {
    color: #2c3e50;
    font-weight: bold;
}
.riqi {
    /* border: 1px solid red; */
    display: flex;
}
.riqi_l {
    color: #000;
    /* width: 28%; */
    text-align: left;
}
.riqi_r {
    color: #b2b2b2;
}
.totle {
    color: #000;
    text-align: right;
}
.put_box {
    text-align: left;
    padding-left: 20px;
    margin: 10px auto;
}
.ele_box {
    line-height: 30px;
    display: flex;
    /* justify-content: space-between; */
    padding: 10px 20px;
}
.input_box {
    text-indent: 1em;
}
.first_bottom {
    display: flex;
    line-height: 40px;
}
.first_canal {
    width: 50%;
    border: 1px solid #cecece;
    border-bottom-left-radius: 10px;
}
.first_ok {
    color: #fb9e58;
    border: 1px solid #cecece;
    width: 50%;
    border-bottom-right-radius: 10px;
}
.no {
    margin: 0 auto;
    border-radius: 5px;
    height: 45px;
    margin-top: 100px;
    font-size: 0.8rem;
}
.back {
    color: #fb9e58;
    margin-left: 6px;
}
.isBack {
    line-height: 40px;
    font-size: 0.8rem;
}
.back_bottom {
    font-size: 0.8rem;
    display: flex;
    line-height: 30px;
}
.nothing {
    margin: 50px;
    font-size: 0.8rem;
}
</style>



